"use client";

import React from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { UploadCloud } from "lucide-react";

interface FileUploadProps {
  setSelectedFile: (file: File | null) => void;
}

const FileUpload: React.FC<FileUploadProps> = ({ setSelectedFile }) => {
  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.files && event.target.files[0]) {
      setSelectedFile(event.target.files[0]);
    }
  };

  return (
    <div className="flex flex-col items-center justify-center h-[40vh] pt-20">
      <div className="bg-gray-600 dark:bg-gray-900 p-6 rounded-lg shadow-lg w-1/2 text-center">
        <h2 className="text-xl font-semibold text-white">Upload your file</h2>

        <label
          htmlFor="file-upload"
          className="mt-4 flex flex-col items-center justify-center p-6 border border-dashed border-gray-300 dark:border-gray-700 rounded-lg cursor-pointer hover:bg-gray-700 transition-all"
        >
          <UploadCloud className="w-10 h-10 text-gray-400" />
          <span className="mt-2 text-gray-300">
            Click or drag a file to upload
          </span>
          <Input
            id="file-upload"
            type="file"
            className="hidden"
            onChange={handleFileChange}
          />
        </label>


        <Button className="mt-4 px-8 py-4 bg-primary text-white hover:bg-primary/50 transition-all">
          Send Data
        </Button>
      </div>
    </div>
  );
};

export default FileUpload;
